<template>
  <section class="main">
    <h1 class="intro">{{ $t("MY_APPS") }} (<a href="https://www.youtube.com/watch?v=5g6-JeZff8I" target="_blank">{{ $t('VIDEO_TUTORIAL') }}</a>)</h1>
    <div class="row">
      <div>
        <p class="g-text">
          {{ $t("MYAPPSTORE_DESC") }}
          <b class="email"
            >Email ID:
            {{ $store.getters.devices[0] && $store.getters.devices[0].email }}
          </b>
        </p>
        <br/>
        <html-with-links
          tag="p"
          class="g-text"
          :html="$t('CUSTOMER_APPS_SHARING_NOTICE')"
        />
        <br/>
        <html-with-links
          tag="p"
          class="g-text"
          :html="$t('APP_IMPORT_EXAMPLE_NOTICE',{href:`https://${BRAND_CONFIG.domain}/my/apps?importUrl=https://example.com/file.ipa`})"
        />
        <div class="header">
          <h2 class="header__title">{{ $t("YOUR APPS") }}</h2>
          <span class="header__storage">
            <template v-for="(limit, idx) in limits">
              {{ limit.label }}: {{ prettysize(limit.usedBytes) }} / {{ prettysize(limit.limitBytes) }}<br :key="idx">
            </template>

            <html-with-links
              v-if="!hasPlus"
              tag="span"
              style="font-size: 12px;"
              :html="'(' + $t('UPGRADE_MY_APPSTORE_STORAGE',
                { href: '/my/plus'}) + ')'"
              />
          </span>
        </div>

        <v-filters v-model="search.filters" />

        <div v-infinite-scroll="loadNextSearchPage">
          <app
            v-for="app in search.result"
            :app="app"
            :key="app.id"
            class="main__app"
            :isInStore="true"
            :disabled="app._isDeleted"
            @deleted="onDeleted(app)"
          />
        </div>

        <v-loader
          v-if="!search.pagination.isLastPage"
          class="loading-spinner"
          :class="{
            'loading-spinner_visible': search.isSearching
          }"
        />
      </div>
      <div class="upload">
        <form class="upload__box">
          <input
            @change="newFilesHandler"
            class="upload__input"
            ref="upload"
            type="file"
            multiple
          />
          <v-button type="primary" @click.native.prevent="select" class="upload__button">
            {{ $t("UPLOAD_IPA_FILES") }}
          </v-button>
          <v-button
            @click.native.prevent="uploadByUrlWithPrompt"
            :loading="uploadingByUrl"
            type="primary"
            class="upload__button"
          >{{ $t("UPLOAD_BY_URL") }}</v-button>
          <span v-if="uploads.length" class="upload__num">
            {{ $t("FILES_SELECTED", { count: uploads.length }) }}
          </span>
          <ul>
            <li v-for="upload in uploads" :key="upload.id" class="upload__item">
              <template v-if="!upload.status">
                <span class="upload__title">{{ upload.name }}</span>
                <span class="upload__progress-wrapper">
                  <span class="upload__progress">
                    <span
                      :style="{ width: upload.progress + '%' }"
                      class="upload__progress-inner"
                    ></span>
                  </span>
                  {{ upload.progress }}%
                </span>
              </template>
              <template v-else>
                <span
                  class="upload__status"
                  :class="{ upload__status_error: upload.error }"
                >
                  {{ upload.status }}
                </span>
                <span class="upload__date">
                  {{ upload.date }}
                  <img
                    v-if="upload.finished"
                    @click="deleteJob(upload.id)"
                    class="upload__delete"
                    src="@/assets/icon-delete.svg"
                  />
                </span>
              </template>
            </li>
          </ul>
        </form>
        <p class="help g-text">
          <img class="help__icon" src="@/assets/icon-info.svg" />
          <html-with-links
            tag="span"
            class="help__text"
            :html="
              $t('CONFIGURE_FEATURES_AT_PAGE', {
                configureLink: '/my/configure',
              })
            "
          ></html-with-links>
        </p>
      </div>
    </div>
  </section>
</template>

<script>
import App from '@/components/App';
import prettysize from 'prettysize';
import HtmlWithLinks from '@/components/HtmlWithLinks';
import VButton from '@/components/Button';
import { mapGetters, mapActions, mapState } from 'vuex';
import infiniteScroll from '@/mixins/infiniteScroll';
import VFilters from './components/VFilters.vue';

export default {
  mixins: [infiniteScroll],
  components: {
    App, HtmlWithLinks, VButton, VFilters,
  },
  data() {
    return {
      stats: {},
      uploads: [],
      uploadingByUrl: false,
      search: {
        apiMethod: 'getIpas',
      },
    };
  },
  computed: {
    ...mapState(['customerProfile']),
    ...mapGetters(['hasPlus']),
    title() {
      return this.$t('MY_APPS_TITLE');
    },
    limits() {
      if (!this.customerProfile.usage) {
        return [];
      }

      return [
        {
          label: this.$t('PRIVATE_APPS'),
          usedBytes: this.customerProfile.usage.private_ipa_storage.used_bytes,
          limitBytes: this.customerProfile.usage.private_ipa_storage.limit_bytes,
        },
        {
          label: this.$t('PUBLIC_APPS'),
          usedBytes: this.customerProfile.usage.public_ipa_storage.used_bytes,
          limitBytes: this.customerProfile.usage.public_ipa_storage.limit_bytes,
        },
      ];
    },
    shouldScheduleGetJobs() {
      return this.uploads.some(upload => !upload.finished);
    },
  },
  methods: {
    prettysize,
    ...mapActions(['getCustomerProfile']),
    select() {
      this.$refs.upload.click();
    },
    generateId() {
      return [...Array(40)].map(() => Math.random().toString(36)[2]).join('');
    },
    newFilesHandler() {
      Array.from(this.$refs.upload.files).forEach(async (file) => {
        const id = this.generateId();
        const app = {
          id,
          name: file.name,
          progress: 0,
          status: '',
          date: '',
          finished: false,
          error: false,
        };
        this.uploads.unshift(app);

        try {
          const response = await this.$api.uploadApp({ file, id }, (e) => {
            app.progress = Math.round((e.loaded * 100) / e.total);
          });

          if (!response.success) {
            app.status = this.$t('UPLOAD_FAILED_WITH_REASON', { reason: response.errors[0].translated });
            app.error = true;
          } else {
            app.status = this.$t('REQUESTING_VALIDATION');
            this.tryScheduleGetJobs();
          }
        } catch {
          app.status = this.$t('UPLOAD_FAILED');
          app.error = true;
        }
      });
    },
    async uploadByUrlWithPrompt() {
      const url = await this.$prompt(this.$t('PLEASE_PROVIDE_URL_TO_IPA'));
      await this.uploadByUrl(url);
    },
    async tryUploadByUrlWithQuery() {
      const url = this.$route.query.importUrl;
      if (!url) {
        return;
      }

      this.$router.replace({ path: this.$route.fullPath, query: { importUrl: undefined } });
      await this.uploadByUrl(url);

      this.$notify({
        type: 'success',
        text: this.$t('APP_IMPORT_STARTED'),
      });
    },
    async uploadByUrl(url) {
      try {
        this.uploadingByUrl = true;
        await this.$api.uploadAppByUrl({ url, id: this.generateId() });
        this.tryScheduleGetJobs();
      } finally {
        this.uploadingByUrl = false;
      }
    },
    deleteJob(id) {
      this.$api.deleteAppJob(id).then(() => {
        const index = this.uploads.findIndex(u => u.id === id);
        this.uploads.splice(index, 1);
      });
    },
    getJobs() {
      return this.$api.getAppsJobs().then((jobs) => {
        jobs.forEach((newJob) => {
          const oldJob = this.uploads.find(u => u.id === newJob.id);
          const date = new Date(newJob.updated_at * 1000).toLocaleString();
          const error = /error/i.test(newJob.status);

          if (!oldJob) {
            this.uploads.push({
              id: newJob.id, status: newJob.status, date, finished: newJob.is_finished, error,
            });
            return;
          }

          oldJob.status = newJob.status;
          oldJob.date = date;
          oldJob.error = error;
          if (newJob.is_finished && !oldJob.finished) {
            oldJob.finished = true;
            this.loadFirstSearchPage();
            this.getCustomerProfile();
          }
        });
      });
    },
    async tryScheduleGetJobs() {
      // eslint-disable-next-line no-underscore-dangle
      if (this._isDestroyed) {
        return;
      }

      try {
        await this.getJobs();
      } finally {
        if (this.shouldScheduleGetJobs) {
          setTimeout(() => {
            this.tryScheduleGetJobs();
          }, 1000);
        }
      }
    },
    onDeleted(app) {
      const idx = this.search.result.find(({ id }) => app.id === id);
      if (idx !== -1) {
        this.search.result.splice(idx, 1);
        this.search.pagination.pageSizeDelta -= 1;
      }

      this.getCustomerProfile();
    },
  },
  mounted() {
    this.getCustomerProfile();
    this.tryScheduleGetJobs();
    this.tryUploadByUrlWithQuery();

    this.$api.getStats().then((response) => {
      this.stats = response;
    });

    this.$api.getCustomerProfile();
  },
};
</script>

